:root {
  --green: #2BAE66FF;
  --white: #FFF;
}

.posts-container {
  width: 1320px;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  padding-top: 60px;
  padding-bottom: 20px;
}

.post-card {
  border-radius: 3px;
  transition: 0.2s ease-out;
  width: 300px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.post-card .post-author-details-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  height: 100%;
  padding: 0 10px;
}
.post-card .post-title, .post-card .post-author {
  margin: 5px 0;
}
.post-card .post-title {
  text-align: left;
  margin-top: 20px;
  font-size: 18px;
  width: 100%;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
.post-card .post-author {
  font-size: 14px;
  color: #777;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
.post-card .home-post-img {
  aspect-ratio: 16/9;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: center;
     object-position: center;
  width: 100%;
  border-radius: 7px;
}
.post-card .img-placeholder {
  background-color: var(--light-gray);
  border-radius: 7px;
  width: 100%;
  aspect-ratio: 16/9;
}

.post-card:hover {
  cursor: pointer;
}

.delete-post-button {
  background: none;
  position: absolute;
  bottom: 20px;
  right: 10px;
  border: none;
  width: 16px;
  height: 16px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.15s ease-in-out;
}

.delete-post-button:hover {
  cursor: pointer;
  scale: 1.2;
}

@media screen and (min-width: 1500px) {
  .post-card {
    width: 400px;
  }
  .post-card .img-placeholder {
    width: 400px;
  }
  .post-card .post-title {
    font-size: 20px;
  }
  .post-card .post-author {
    font-size: 16px;
  }
}
@media screen and (max-width: 1500px) {
  .posts-container {
    width: 1000px;
  }
}
@media screen and (max-width: 1200px) {
  .posts-container {
    width: 600px;
    padding-top: 40px;
  }
  .post-card {
    width: 250px;
  }
  .post-card .img-placeholder {
    width: 250px;
  }
  .post-card .post-author {
    font-size: 12px;
  }
  .post-card .post-author, .post-card .post-title {
    margin: 3px 0;
  }
  .post-card .post-title {
    font-size: 16px;
    margin-top: 16px;
  }
}
@media screen and (max-width: 1050px) {
  .posts-container {
    padding-top: 80px;
  }
}
@media screen and (max-width: 650px) {
  .posts-container {
    width: 60vw;
  }
  .post-card {
    width: 60vw;
  }
  .post-card .img-placeholder {
    width: 60vw;
  }
}/*# sourceMappingURL=Home.css.map */